import { useTranslation } from "react-i18next";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import styleIncome from "../../../FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useCallback, useEffect, useState } from "react";
import { useAdmissionCreateApplication } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import FeeStatisticsDepartmentFilter from "../../../FinanceManager/FeeStatistics/FeeStatisticsDepartmentFilter";
import FeeStatisticsBatchFilter from "../../../FinanceManager/FeeStatistics/FeeStatisticsBatchFilter";
import FeeStatisticsStandardFilter from "../../../FinanceManager/FeeStatistics/FeeStatisticsStandardFilter";
import CreateButton from "../../../../Ui/Button/CreateButton";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import FormInput from "../../../../Ui/Input/FormInput";
import CreateApplicationOtherInfo from "../CreateApplicationOtherInfo";
import SelectFeeStructure from "../../../FinanceManager/FeesStructure/SelectFeeStructure/SelectFeeStructure";
import Modal from "../../../../Ui/Modal/Modal";
import SelectDivisions from "../../SelectDivisions/SelectDivisions";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import SubjectCriteriaGroupDorpdown from "../../../SubjectCriteriaGroup/SubjectCriteriaGroupDorpdown";
const CreateDirectApplication = ({
  onClose,
  fid,
  instituteId,
  aid,
  onRefetch,
  onParentClose,
  isEdit,
  onEdit,
  applicationDetail,
}) => {
  const { t } = useTranslation();
  const [step, setStep] = useState(0);
  const [state, setState] = useState({
    applicationName: "",
    gr_initials: "",
    department: "",
    batch: "",
    classMaster: "",
    direct_linked_structure: "",
    direct_attach_class: "",
    subject_selected_group: [],
  });
  const [disabled, setDisabled] = useState(false);
  const [errorField, setErrorField] = useState("");

  const [amissionCreateApplication] = useAdmissionCreateApplication();

  useEffect(() => {
    if (isEdit && applicationDetail) {
      setState((prev) => ({
        ...prev,
        applicationName: applicationDetail?.applicationName ?? "",
        gr_initials: applicationDetail?.gr_initials ?? "",
        department: applicationDetail?.applicationDepartment ?? "",
        batch: applicationDetail?.applicationBatch ?? "",
        classMaster: applicationDetail?.applicationMaster ?? "",
        direct_linked_structure:
          applicationDetail?.direct_linked_structure ?? "",
        direct_attach_class: applicationDetail?.direct_attach_class ?? "",
        subject_selected_group: applicationDetail?.subject_selected_group ?? [],
      }));
    }
  }, [isEdit, applicationDetail]);
  const onSelectDepartment = useCallback((value) => {
    setState((prev) => ({
      ...prev,
      department: value,
      batch: "",
      classMaster: "",
    }));
  }, []);
  const onSelectBatch = useCallback((value) => {
    setState((prev) => ({
      ...prev,
      batch: value,
    }));
  }, []);

  const onSelectStandard = useCallback((value) => {
    setState((prev) => ({
      ...prev,
      classMaster: value,
    }));
  }, []);
  const onInputChange = (e) => {
    if (e.target.name === "gr_initials") {
      if (e.target.value?.length <= 10)
        setState((prev) => ({
          ...prev,
          [e.target.name]: e.target.value,
        }));
    } else
      setState((prev) => ({
        ...prev,
        [e.target.name]: e.target.value,
      }));
  };

  const onSelectedStrucutre = (val) => {
    setState((prev) => ({
      ...prev,
      direct_linked_structure: val,
    }));
    setStep(2);
  };

  const onSelectDivision = (val) => {
    setState((prev) => ({
      ...prev,
      direct_attach_class: val,
    }));
    setStep(3);
  };
  const onSkip = () => {
    setStep(3);
  };
  const onNext = () => {
    let validation = requiredField(state, [
      "gr_initials",
      "direct_attach_class",
      "direct_linked_structure",
      "subject_selected_group",
    ]);
    if (existingRequiredField(validation)) {
      setStep(1);
    } else {
      setErrorField(validation);
    }
  };

  const onCreateApplication = (otherargs) => {
    if (isEdit) {
      onEdit({
        application_type: "Direct Admisison",
        applicationName: state.applicationName,
        gr_initials: state.gr_initials,
        applicationDepartment: state.department?._id,
        applicationBatch: state.batch?._id,
        applicationMaster: state.classMaster?._id,
        direct_linked_structure: state.direct_linked_structure?._id,
        direct_attach_class: state.direct_attach_class?._id,
        subject_selected_group: state.subject_selected_group?.[0]?._id
          ? [state.subject_selected_group?.[0]?._id]
          : [],
        ...otherargs,
      });
    } else {
      if (aid) {
        setDisabled((pre) => !pre);
        amissionCreateApplication({
          aid: aid,
          application: {
            application_type: "Direct Admisison",
            applicationName: state.applicationName,
            gr_initials: state.gr_initials,
            applicationDepartment: state.department?._id,
            applicationBatch: state.batch?._id,
            applicationMaster: state.classMaster?._id,
            direct_linked_structure: state.direct_linked_structure?._id,
            direct_attach_class: state.direct_attach_class?._id,
            subject_selected_group: state.subject_selected_group?.[0]?._id
              ? [state.subject_selected_group?.[0]?._id]
              : [],
            ...otherargs,
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onParentClose();
          })
          .catch({});
      }
    }
  };
  const onGroupSelect = (dt) => {
    setState((prev) => ({
      ...prev,
      subject_selected_group: [dt],
    }));
  };
  return (
    <>
      {step === 0 ? (
        <Modal>
          <div className={styleIncome.modal_container}>
            <UserMemberContentWrapper>
              <div className={styleIncome.modal_container_outer_header}>
                <div className={styleIncome.modal_container_header}>
                  <h6>{t("new_direct_admission_application")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
                marginBottom: "1rem",
              }}
            >
              <FormInput
                label={t("application_name")}
                placeholder={t("please_enter_application_name")}
                name="applicationName"
                value={state.applicationName}
                type="text"
                onChange={onInputChange}
                errorShow={errorField["applicationName"]}
              />

              <FormInput
                label={t("application_initials")}
                placeholder={t("department_initials_placeholder")}
                name="gr_initials"
                value={state.gr_initials}
                type="text"
                onChange={onInputChange}
              />
              <FeeStatisticsDepartmentFilter
                fid={fid}
                onSelect={onSelectDepartment}
                onlyDepartment
                errorShow={errorField["department"]}
                isDenied={isEdit}
              />
              <FeeStatisticsBatchFilter
                did={state.department?._id}
                onSelect={onSelectBatch}
                onlyBatch
                errorShow={errorField["batch"]}
                isDenied={isEdit}
              />
              <FeeStatisticsStandardFilter
                did={state.department?._id}
                onSelect={onSelectStandard}
                instituteId={instituteId}
                errorShow={errorField["classMaster"]}
                isDenied={isEdit}
              />
              <SubjectCriteriaGroupDorpdown
                onSelect={onGroupSelect}
                admissionId={aid}
                errorShow={errorField["subject_selected_group"]}
                defalutValue={
                  state.subject_selected_group?.[0]?.subject_group_name
                }
                isDenied={isEdit}
              />
              <CreateButton label="next" onAction={onNext} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      ) : step === 1 ? (
        <SelectFeeStructure
          onClose={() => setStep(0)}
          did={state.department?._id}
          bid={state?.batch?._id}
          cmid={state?.classMaster?._id}
          selectedStructure={state.direct_linked_structure?._id}
          onSelectedStrucutre={onSelectedStrucutre}
          oneStep
        />
      ) : step === 2 ? (
        <SelectDivisions
          onClose={() => setStep(1)}
          onSkip={onSkip}
          bid={state?.batch?._id}
          selectedDivision={state.direct_attach_class?._id}
          onSelectDivision={onSelectDivision}
        />
      ) : step === 3 ? (
        <CreateApplicationOtherInfo
          applicationType="Direct Application"
          onClose={() => setStep((prev) => (prev -= 1))}
          onCreateApplication={onCreateApplication}
          applicationDetail={applicationDetail}
          isEdit={isEdit}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default CreateDirectApplication;
