import { useTranslation } from "react-i18next";
import { useState } from "react";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useAdmissionIncompleteApplication } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";

const CompleteApplicationMenu = ({ onClose, appId, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [admissionIncompleteApplication] = useAdmissionIncompleteApplication();

  const onMarkIncomplete = () => {
    if (appId) {
      setDisabled((pre) => !pre);
      admissionIncompleteApplication({
        appId: appId,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onMarkIncomplete}>{t("mark_as_ongoing")}</h6>
      </ModalMenu>
    </>
  );
};

export default CompleteApplicationMenu;
