import { useTranslation } from "react-i18next";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import styleIncome from "../../FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useEffect, useState } from "react";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import FormInput from "../../../Ui/Input/FormInput";
import FormTextarea from "../../../Ui/Input/FormTextarea";
import FormFile from "../../../Ui/Input/FormFile";
import { useFileUpload } from "../../../../hooks/authentication-api";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Modal from "../../../Ui/Modal/Modal";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../Utils/Functions/dateUtility";
const CreateApplicationOtherInfo = ({
  onClose,
  applicationType,
  onCreateApplication,
  applicationDetail,
  isEdit,
}) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [file, setFile] = useState("");

  const [state, setState] = useState({
    applicationSeats: "",
    applicationStartDate: "",
    applicationEndDate: "",
    applicationAbout: "",
    applicationPhoto: "",
  });
  const [fileUpload] = useFileUpload();

  useEffect(() => {
    if (isEdit && applicationDetail) {
      setState((prev) => ({
        ...prev,
        applicationSeats: applicationDetail?.applicationSeats ?? "",
        applicationStartDate: applicationDetail?.applicationStartDate
          ? slashDateFormat(applicationDetail?.applicationStartDate)
          : "",
        applicationEndDate: applicationDetail?.applicationEndDate
          ? slashDateFormat(applicationDetail?.applicationEndDate)
          : "",
        applicationAbout: applicationDetail?.applicationAbout ?? "",
        applicationPhoto: applicationDetail?.applicationPhoto ?? "",
      }));
    }
  }, [isEdit, applicationDetail]);
  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          setFile({
            name: e.target.files[0]?.name,
            key: res.data?.imageKey,
            type: e.target.files[0]?.type,
          });
          setState((prev) => ({
            ...prev,
            applicationPhoto: res.data?.imageKey,
          }));

          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onStartDate = (value) => {
    setState((prev) => ({
      ...prev,
      applicationStartDate: slashDateFormat(value),
    }));
  };
  const onEndDate = (value) => {
    setState((prev) => ({
      ...prev,
      applicationEndDate: slashDateFormat(value),
    }));
  };

  const onSubmit = () => {
    if (applicationType === "Normal Application") {
      onCreateApplication({
        ...state,
        applicationStartDate: state?.applicationStartDate
          ? ymdSlashDateFormat(state?.applicationStartDate)
          : "",
        applicationEndDate: state?.applicationEndDate
          ? ymdSlashDateFormat(state?.applicationEndDate)
          : "",
      });
    } else {
      let validation = requiredField(state, [
        "applicationStartDate",
        "applicationEndDate",
        "applicationAbout",
        "applicationPhoto",
      ]);
      if (existingRequiredField(validation)) {
        onCreateApplication({
          ...state,
          applicationStartDate: state?.applicationStartDate
            ? ymdSlashDateFormat(state?.applicationStartDate)
            : "",
          applicationEndDate: state?.applicationEndDate
            ? ymdSlashDateFormat(state?.applicationEndDate)
            : "",
        });
      } else {
        setErrorField(validation);
      }
    }
  };
  return (
    <>
      <Modal>
        <div className={styleIncome.modal_container}>
          <UserMemberContentWrapper>
            <div className={styleIncome.modal_container_outer_header}>
              <div className={styleIncome.modal_container_header}>
                <h6>{t("create_new_application")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
              marginBottom: "1rem",
            }}
          >
            <FormInput
              label={t(
                applicationType !== "Normal Application"
                  ? "available_seat_not_optional"
                  : "available_seat"
              )}
              placeholder={t("please_enter_available_seat")}
              name="applicationSeats"
              value={state.applicationSeats}
              type="tel"
              onChange={onInputChange}
              errorShow={errorField["applicationSeats"]}
            />
            <FormCalendarInput
              label={t("application_start_date")}
              placeholder={t("please_select_the_date")}
              name={"applicationStartDate"}
              value={state.applicationStartDate}
              onChange={onStartDate}
            />
            <FormCalendarInput
              label={t("application_end_date")}
              placeholder={t("please_select_the_date")}
              name={"applicationEndDate"}
              value={state.applicationEndDate}
              onChange={onEndDate}
            />
            <FormTextarea
              label={t("application_info_required")}
              placeholder={t("write_something")}
              name={"applicationAbout"}
              value={state.applicationAbout}
              type={"text"}
              onChange={onInputChange}
            />
            <FormFile
              label={t("brochure")}
              name={"brochure_file"}
              value={
                file?.name
                  ? {
                      name: file?.name,
                    }
                  : ""
              }
              onChange={onFileUpload}
            />
            <CreateButton label="create" onAction={onSubmit} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default CreateApplicationOtherInfo;
