import style from "./OngoingAdmission.module.css";
import {
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useState } from "react";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import OngoingAdmissionCardMenu from "./OngoingAdmissionCardMenu";
import CompleteApplicationMenu from "../CompleteApplication/CompleteApplicationMenu";
import { admissionOneTabAccess } from "../../../Constant/Admission/admission_constant";

const OngoingAdmissionCard = ({
  application,
  onRefetch,
  aid,
  isAuthorization,
}) => {
  const [add, setAdd] = useState(false);
  const [completed, setCompleted] = useState(false);
  const navigate = useNavigate();
  const params = useParams();
  const getQuery = useLocation();
  const onMenu = (e) => {
    e.stopPropagation();
    if (authorized[isAuthorization]) setCompleted(true);
    else setAdd(true);
  };
  const onClose = () => {
    setAdd(false);
  };
  const onCloseComplete = () => {
    setCompleted(false);
  };
  const onNavigate = (e, value) => {
    if (e) e.stopPropagation();
    let url = "";
    if (value)
      url =
        getQuery.state?.urlOffSet === "Institute"
          ? `/q/${params.username}/institute/member/admission?type=ongoing-detail&n=${value}`
          : `/q/${params.username}/member/staff/admission?type=ongoing-detail&n=${value}`;
    else
      url =
        getQuery.state?.urlOffSet === "Institute"
          ? `/q/${params.username}/institute/member/admission?type=ongoing-detail`
          : `/q/${params.username}/member/staff/admission?type=ongoing-detail`;

    navigate(url, {
      state: {
        ...getQuery.state,
        appId: application?._id,
        applicationName: application?.applicationName,
        isAuthorization: isAuthorization,
      },
    });
  };

  // console.info("application", application);
  return (
    <>
      <div
        className={style.ongoing_card}
        onClick={onNavigate}
        style={{
          position: "relative",
        }}
      >
        <div className={style.ongoing_card_inner}>
          <img
            alt="Ongoing App"
            src={`${assestsUserAdmissionUrl}/application.svg`}
          />
          <div className={style.ongoing_card_text}>
            <h6>{application?.applicationName ?? ""}</h6>
            <p>{application?.applicationDepartment?.dName ?? ""}</p>
          </div>
        </div>
        <div className={style.ongoing_card_count_date}>
          {getQuery.state?.subRole ? (
            <div className={style.ongoing_card_count_container}>
              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "Application" && (
                <h6 onClick={() => onNavigate("")} title="Application">
                  {application?.receievedCount ?? 0}
                </h6>
              )}
              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "Selected" && (
                <h6
                  onClick={(e) => onNavigate(e, "document")}
                  title="Docs Collect"
                >
                  {application?.selectCount ?? 0}
                </h6>
              )}
              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "Confirmed" && (
                <h6
                  onClick={(e) => onNavigate(e, "fees")}
                  title="Fees Collection"
                >
                  {application?.fee_collect_count ?? 0}
                </h6>
              )}
              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "Confirmed" && (
                <h6
                  onClick={(e) => onNavigate(e, "confirmed")}
                  title="Confirmed"
                >
                  {application?.confirmCount ?? 0}
                </h6>
              )}

              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "Confirmed" && (
                <h6 onClick={(e) => onNavigate(e, "review")} title="Review">
                  {application?.review_count ?? 0}
                </h6>
              )}

              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "Allotted" && (
                <h6
                  onClick={(e) => onNavigate(e, "allottment")}
                  title="Allottment"
                >
                  {application?.allotCount ?? 0}
                </h6>
              )}

              {admissionOneTabAccess[getQuery.state?.subRole] ===
                "Cancelled" && (
                <h6
                  onClick={(e) => onNavigate(e, "cancelled")}
                  title="Cancelled"
                >
                  {application?.cancelCount ?? 0}
                </h6>
              )}
            </div>
          ) : (
            <div className={style.ongoing_card_count_container}>
              <h6 onClick={() => onNavigate("")} title="Application">
                {application?.receievedCount ?? 0}
              </h6>
              <h6
                onClick={(e) => onNavigate(e, "document")}
                title="Docs Collect"
              >
                {application?.selectCount ?? 0}
              </h6>
              <h6
                onClick={(e) => onNavigate(e, "fees")}
                title="Fees Collection"
              >
                {application?.fee_collect_count ?? 0}
              </h6>
              <h6 onClick={(e) => onNavigate(e, "confirmed")} title="Confirmed">
                {application?.confirmCount ?? 0}
              </h6>
              <h6 onClick={(e) => onNavigate(e, "review")} title="Review">
                {application?.review_count ?? 0}
              </h6>
              <h6
                onClick={(e) => onNavigate(e, "allottment")}
                title="Allottment"
              >
                {application?.allotCount ?? 0}
              </h6>
              <h6 onClick={(e) => onNavigate(e, "cancelled")} title="Cancelled">
                {application?.cancelCount ?? 0}
              </h6>
            </div>
          )}
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onMenu}
          className={style.menu_icon}
          title="Menu"
        />
      </div>
      {add && (
        <OngoingAdmissionCardMenu
          onClose={onClose}
          aid={aid}
          appId={application?._id}
          onRefetch={onRefetch}
          formId={application?.student_form_setting}
          menuData={application}
        />
      )}
      {completed && (
        <CompleteApplicationMenu
          onClose={onCloseComplete}
          appId={application?._id}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default OngoingAdmissionCard;

const authorized = {
  isCompleted: true,
};
