import { useTranslation } from "react-i18next";
import { useState } from "react";
import ModalMenu from "../../../Ui/Modal/ModalMenu";
import {
  useAdmissionOneOngoingApplication,
  useAdmissionRemoveApplication,
  useAdmissionUpdateApplication,
  useInstituteUnPinApplication,
} from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import CreateDirectApplication from "./CreateDirectApplication/CreateDirectApplication";
import CreateApplicationStepWise from "./CreateApplicationStepWise";
import { Link, useLocation, useParams } from "react-router-dom";
import PinApplication from "../PinApplication/PinApplication";
const OngoingAdmissionCardMenu = ({
  onClose,
  aid,
  appId,
  onRefetch,
  formId,
  menuData,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [step, setStep] = useState("");
  const getQuery = useLocation();
  const params = useParams();
  const [admissionRemoveApplication] = useAdmissionRemoveApplication();
  const [admissionUpdateApplication] = useAdmissionUpdateApplication();
  const [instituteUnPinApplication] = useInstituteUnPinApplication();

  const {
    admissionOneOngoingApplication,
    admissionOneOngoingApplicationRefetch,
  } = useAdmissionOneOngoingApplication({
    appId: appId,
    skip: !appId,
  });

  const onDelete = () => {
    if (aid && appId) {
      setDisabled((pre) => !pre);
      admissionRemoveApplication({
        aid: aid,
        appId: appId,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val) => {
    if (appId) {
      setDisabled((pre) => !pre);
      onOpenEdit();
      admissionUpdateApplication({
        appId: appId,
        updateApplication: val,
      })
        .then(() => {
          admissionOneOngoingApplicationRefetch();
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  const onUnPinned = () => {
    if (aid && menuData?.pin?.flow) {
      setDisabled((pre) => !pre);
      instituteUnPinApplication({
        aid: aid,
        bodyParameter: {
          did:
            menuData?.pin?.flow === "DEPENDENT"
              ? menuData?.pin?.flow_id
              : menuData?._id,
          flow: menuData?.pin?.flow,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  // console.info("menuData", aid);
  const onCloseStep = () => {
    setStep("");
  };
  // console.info("admissionOneOngoingApplication", menuData);
  return (
    <>
      {edit ? (
        <>
          {admissionOneOngoingApplication?.application_type ===
          "Direct Admisison" ? (
            <CreateDirectApplication
              onClose={onOpenEdit}
              applicationDetail={admissionOneOngoingApplication}
              onEdit={onEdit}
              isEdit
            />
          ) : (
            <CreateApplicationStepWise
              onClose={onOpenEdit}
              applicationDetail={admissionOneOngoingApplication}
              onEdit={onEdit}
              isEdit
            />
          )}
        </>
      ) : step === 1 ? (
        <PinApplication
          onClose={onCloseStep}
          aid={aid}
          applicationId={appId}
          onParentClose={onClose}
          onReftech={onRefetch}
        />
      ) : (
        <ModalMenu
          onClose={onClose}
          disabled={disabled}
          customStyle={{
            width: "18rem",
          }}
        >
          <Link
            to={`/q/${params.username}/member/staff/admission?type=application-form`}
            state={{
              ...getQuery.state,
              formId: formId,
              applicationId: appId,
            }}
          >
            {t("application_form")}
          </Link>
          {menuData?.pin?.status === "Pinned" ? (
            <h6 onClick={onUnPinned}>{t("unpin_application")}</h6>
          ) : (
            <h6 onClick={() => setStep(1)}>{t("pin_application")}</h6>
          )}

          <h6 onClick={onOpenEdit}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default OngoingAdmissionCardMenu;
