import { useTranslation } from "react-i18next";
import Loading from "../../Loader/Loading";
import { useAdmissionOuterGroupList } from "../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import CustomSelectSubjectGroup from "../../Ui/Select/CustomSelectSubjectGroup";

const SubjectCriteriaGroupDorpdown = ({
  admissionId,
  onSelect,
  errorShow,
  defalutValue,
  isDenied,
  customStyleContainer,
}) => {
  const { t } = useTranslation();
  const { admissionOuterGroupList, admissionOuterGroupListLoading } =
    useAdmissionOuterGroupList({
      data: {
        aid: admissionId,
        page: 1,
        limit: 1000,
        search: "",
      },
      skip: !admissionId,
    });
  return (
    <>
      <CustomSelectSubjectGroup
        selectLabel={t("select_group")}
        selectedValue={t("select_subject_optional_group")}
        options={
          admissionOuterGroupList?.list?.length > 0
            ? admissionOuterGroupList?.list
            : []
        }
        onClick={onSelect}
        isFull
        errorShow={errorShow}
        defalutValue={defalutValue}
        isDenied={isDenied}
        customStyleContainer={customStyleContainer}
      />

      {admissionOuterGroupListLoading && <Loading />}
    </>
  );
};

export default SubjectCriteriaGroupDorpdown;
