import { useTranslation } from "react-i18next";
import { useDepartmentBatchAllClass } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../PlateformUser/PlateformUser.module.css";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import SelectDivisionsCard from "./SelectDivisionsCard";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import CreateButton from "../../../Ui/Button/CreateButton";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import AddButtonWrapper from "../../../Ui/Button/AddButtonWrapper";
const SelectDivisions = ({
  onClose,
  bid,
  onSkip,
  onSelectDivision,
  selectedDivision,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const { departmentBatchAllClass, departmentBatchAllClassLoading } =
    useDepartmentBatchAllClass({
      data: {
        page: page,
        limit: 10,
        search: search,
        bid: bid,
      },
      skip: !bid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (departmentBatchAllClass?.list?.length) {
      let obj = nextPage(departmentBatchAllClass?.list?.length);
      setState(() => obj);
    }
  }, [departmentBatchAllClass?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  // console.info("departmentBatchAllClass", departmentBatchAllClass);
  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("select_class_optional")}</h6>
              </div>
              <AddButtonWrapper>
                <CreateButton
                  label="skip"
                  customStyle={{
                    margin: "0",
                    padding: "0.2rem 0.7rem",
                  }}
                  onAction={onSkip}
                />
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                  className={style.close_icon}
                />
              </AddButtonWrapper>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
              marginBottom: "1rem",
            }}
          >
            <div className={style.assign_search_container}>
              <input
                type="text"
                placeholder={t("search")}
                className={style.assign_search_input}
                onChange={onSearchEvent}
                style={{
                  width: "100%",
                }}
              />
              <img
                src={`${assestsNavbarUrl}/navbar-search.svg`}
                alt="search icon"
              />
            </div>

            {departmentBatchAllClass?.list?.map((cls, index) =>
              departmentBatchAllClass?.list?.length === index + 1 ? (
                <div key={cls?._id} ref={ref}>
                  <SelectDivisionsCard
                    cls={cls}
                    selectedCls={selectedDivision}
                    onSelect={onSelectDivision}
                  />
                </div>
              ) : (
                <SelectDivisionsCard
                  cls={cls}
                  key={cls?._id}
                  selectedCls={selectedDivision}
                  onSelect={onSelectDivision}
                />
              )
            )}

            {departmentBatchAllClassLoading ? (
              search ? (
                <QvipleLoader />
              ) : (
                <Loading />
              )
            ) : search ? (
              departmentBatchAllClass?.list?.length === 0 && (
                <EmptyMessage
                  title="No classes found related this search."
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                />
              )
            ) : (
              departmentBatchAllClass?.list?.length === 0 && (
                <EmptyMessage
                  title="No classes."
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                />
              )
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default SelectDivisions;
